import api from '../../api/index'
export default {
    name:'pass',
    data(){
        return{
            inputlogin:'',
            inputPass:'',
            confirmPwd:''
        }
    },
    methods:{
        // 确认密码
        confirmPass(){
            let password = {
                password:this.inputlogin,
                newPwd:this.inputPass,
                confirmPwd:this.confirmPwd
            }
            api.editorPass(password).then(res=>{
                if(res.code == 0){
                    this.$message({
                        showClose: true,
                        message: res.msg,
                        type: 'success'
                    })
                }else{
                    this.$message({
                        showClose: true,
                        message: res.msg,
                        type: 'warning'
                    })
                }
                
            })
        }
    }
}



